#screen-wrapper {
    position: fixed;
    width: 100vw;
    height: 100vh;
    z-index: 100;
    background-color: rgba(0,255,0,0.3);

    display: flex;
    justify-content:center;

    overflow-y: scroll;
}

#preview-cv {
    position: relative;
    width: fit-content;
    height: fit-content;

    margin: 100px auto;
}

